import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
export default class ResponsiveDialog extends React.Component {
  state = { open: false, password: "" }
  handleClickOpen = () => {
    this.setState({ open: true })
  }
  handleClose=()=>{
    this.setState({open:false})
  }
  authroisingPassword = (e) => {
    this.setState({ password: e.target.value })
  }
  handleSubmit=()=>{
    if(this.state.password==="admin"){
      this.props.authrisationVerified()
    }
    this.setState({open:false})
  }
  render() {
    return (
      <div>
        <Button variant="outlined" onClick={this.handleClickOpen}>
          Admin
        </Button>
        <Dialog
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">{"Enter The Password"}</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="challenge"
              // defaultValue={this.state.challengeSolved}
              // label="Number of Challenge Solved"
              onChange={e => this.authroisingPassword(e)}
              type="password"
              required={true}
              fullWidth
            />
          </DialogContent>
          <DialogActions>
            <Button autoFocus onClick={this.handleClose} color="primary">
              Discard
          </Button>
            <Button onClick={this.handleSubmit} color="primary" autoFocus>
              Submit
          </Button>
          </DialogActions>
        </Dialog>
      </div >
    );
  }
}
