import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import Button from '@material-ui/core/Button';
import TableRow from '@material-ui/core/TableRow';
import "../App.css"
import { Link } from "react-router-dom";
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
export default class TableCandidates extends React.Component {
  state = {
    candidates: []
  }

  render() {
    return (
      <div className="flex">
        <div>
          <Table style={{ overflow: "scroll" }}>
            <TableHead>
              <TableRow>
                <TableCell style={{ paddingLeft: 10 }}  >Name</TableCell>
                <TableCell style={{ paddingLeft: 10 }} align="right">Votes</TableCell>
                <TableCell style={{ paddingLeft: 10 }} align="right">Rank</TableCell>
                <TableCell style={{ paddingLeft: 10 }} >Votes</TableCell>
                {(this.props.auth) ? <TableCell style={{ paddingLeft: 10 }} >Delete</TableCell> : <TableCell></TableCell>}
              </TableRow>
            </TableHead>
            <TableBody>
              {
                (this.props.candidates.length) ? this.props.candidates.map((candidate, index) => {
                  return <TableRow>
                    <TableCell style={{ paddingLeft: 10 }} ><Link to={{pathname:`/${candidate._id}`, state:{auth:this.props.auth}}} style={{ textDecoration: "none" }}>{candidate.name}</Link></TableCell>
                    <TableCell style={{ paddingLeft: 10 }} >{candidate.votes}</TableCell>
                    <TableCell style={{ paddingLeft: 10 }} >rank</TableCell>
                    <TableCell style={{ paddingLeft: 10 }} ><Button disabled={this.props.disableVote} onClick={() => this.props.vote(candidate, index)} variant="contained">Vote</Button></TableCell>
                    {(this.props.auth) ?
                      <TableCell style={{ paddingLeft: 10 }} ><IconButton onClick={e => this.props.deleteCandidate(candidate, index)} aria-label="delete">
                        <DeleteIcon />
                      </IconButton></TableCell> : <TableCell></TableCell>}
                  </TableRow>

                }) : <div></div>
              }
            </TableBody>
          </Table>
        </div>
      </div>
    );
  }
}
