import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';

import Grid from '@material-ui/core/Grid';
import Logo from "../assets/images/vote-for-blog-800x491.jpg";
import Button from "@material-ui/core/Button";
import {Link} from "react-router-dom";
import Ip from "ip";
class Appbar extends React.Component {
  constructor() {
    super();
  }
  componentDidMount() {
    fetch('http://localhost:8000/candidate')
      .then(res => res.json())
      .then(res => {
        let owner = res.filter(res => res.ip === Ip.address())
        owner = owner.length ? owner[0] : {}
        this.setState({ owner: owner })
      })
  }
  render() {
    return (
      <AppBar position="static">
        <Toolbar>
          <Grid container
            direction="row"
            justify="space-around"
            alignItems="center"
          >
            <Grid item spacing={3}>
              <Link to="/"><img src={Logo} style={{ height: 50, width: 130 }} /></Link>
            </Grid>
            <Grid item spacing={6}>
            </Grid>
            <Grid item spacing={3}><Link to="/"><Button>Home</Button></Link></Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      // </div>
    );
  }
}
export default Appbar;
