import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import Rating from '@material-ui/lab/Rating';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Ip from "ip";
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
export default class ResponsiveDialog extends React.Component {
  state = { name: "", challengeSolved: 0, open: false, value: 1, stack: "", expertiseLevel: 0, rating: 0, stacks: [] }
  handleClickOpen = () => { this.setState({ open: true }) };

  handleClose = () => { this.setState({ open: false,Ip:"" }) };
  handleSubmit = () => {
    var data;
    (!this.props.auth)?
    data = {
      name: this.state.name,
      challengesSolved: this.state.challengeSolved,
      candidateExpertiseLevel: this.state.expertiseLevel,
      votes: 0,
      hasVoted: false,
      ip: Ip.address(),
      expertIn: this.state.stacks
    }:
    data = {
      name: this.state.name,
      challengesSolved: this.state.challengeSolved,
      candidateExpertiseLevel: this.state.expertiseLevel,
      votes: 0,
      hasVoted: false,
      ip: this.state.ip,
      expertIn: this.state.stacks
    }
    console.log(data)
    fetch('http://localhost:8000/candidate', {
      method: 'POST',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': "*"
      },
      body: JSON.stringify(data)
    }).then(res => res.json())
      .then(res => {
        console.log(res.body)
        this.props.addCandidate(res);
      })
    this.setState({ open: false })
  }
  changeRating = (e) => {
    this.setState({ rating: e.target.value })
  }
  changeIp=(e)=>{
    this.setState({ip:e.target.value})
  }
  expertiseLevelChange = (e) => {
    this.setState({ expertiseLevel: e.target.value })
  }
  changeStack = (e) => {
    this.setState({ stack: e.target.value })
  }
  addStackInArray = () => {
    var tempArray = this.state.stacks;
    tempArray.push({ value: this.state.stack, rating: this.state.rating })
    this.setState({ stacks: tempArray })
  }
  changeName = (e) => {
    this.setState({ name: e.target.value })
  }
  changeChallengeSolved = (e) => {
    this.setState({ changeChallengeSolved: e.target.value })
  }
  deleteStack = (stack, index) => {
    let tempStacks = this.state.stacks;
    tempStacks.splice(index, 1)
    this.setState({ stacks: tempStacks })
  }
  render() {
    return (
      <div>

        <Button variant="outlined" onClick={this.handleClickOpen}>
          {(this.props.auth) ? "Add new User" : "Register"}
        </Button>
        <Dialog
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="responsive-dialog-title"
        >
          <DialogTitle id="responsive-dialog-title">{"Register Candidate"}</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Name"
              type="text"
              onChange={(e) => this.changeName(e)}
              fullWidth
            />
            <TextField
              autoFocus
              margin="dense"
              id="challenge"
              defaultValue={this.state.challengeSolved}
              label="Number of Challenge Solved"
              onChange={e => this.changeChallengeSolved(e)}
              type="number"
              required={true}
              fullWidth
            />{
              (this.props.auth) ?
                <TextField
                  autoFocus
                  margin="dense"
                  id="challenge"
                  defaultValue={Ip.address()}
                  label="Ip"
                  value={this.state.Ip}
                  onChange={e => this.changeIp(e)}
                  type="text"
                  required={true}
                  fullWidth
                />:<></>
  }
            <Box style={{ paddingTop: 20 }} mb={3} borderColor="transparent">
              <Typography component="legend">Expertise Level</Typography>
              <Rating
                defaultValue={0}
                name="expertise"
                value={this.state.expertiseLevel}
                onChange={e => this.expertiseLevelChange(e)}
              />
            </Box>
            <Box mb={3} borderColor="transparent">
              <Typography component="legend">Expert In </Typography>
              <TextField
                autoFocus
                margin="none"
                id="stack"
                type="text"
                onChange={e => this.changeStack(e)}
                required={true}
              />
              <Rating
                name="simple-controlled"
                value={this.state.rating}
                style={{ paddingRight: 20 }}
                onChange={e => this.changeRating(e)}
                defaultValue={0}
                required={true}
              />
              <Button variant="contained" onClick={e => this.addStackInArray(e)}>Add</Button>
            </Box>
            {
              this.state.stacks.map((stack, index) =>
                <div>
                  <Box mb={3} borderColor="transparent">
                    <TextField
                      margin="dense"
                      value={stack.value}
                      disabled={true}
                      id="stack"
                      type="text"
                    />
                    <Rating
                      disabled={true}
                      name="simple-controlled"
                      value={stack.rating}
                      defaultValue={0}
                    />
                    <IconButton onClick={e => this.deleteStack(stack, index)} aria-label="delete">
                      <DeleteIcon />
                    </IconButton>
                  </Box>
                </div>
              )
            }
          </DialogContent>
          <DialogActions>
            <Button autoFocus onClick={this.handleClose} color="primary">
              Discard
          </Button>
            <Button onClick={this.handleSubmit} color="primary" autoFocus>
              Submit
          </Button>
          </DialogActions>
        </Dialog>
      </div >
    );
  }
}
