import React from "react";
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import identicon from "identicon";
import Rating from "@material-ui/lab/Rating";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete"
import Button from "@material-ui/core/Button";
import Ip from "ip";

import { CardActionArea, Container, TextField, CardActions } from "@material-ui/core";
class Candidate extends React.Component {
  constructor(props) {
    super(props)
  }
  state = {
    candidate: {},
    image: "",
    edit: false,
    editDisabled: false
  }
  componentDidMount() {
    console.log(this.props)
    fetch(`http://localhost:8000/candidate/${this.props.match.params.id}`)
      .then(res => res.json())
      .then(res => {
        console.log(res)
        var image;
        identicon.generate({ id: res.name, size: 150 }, (err, buffer) => {
          image = new Image()
          image.src = buffer;
        })
        var disabled;
        res.ip == Ip.address() || this.props.location.state.auth ? disabled = false : disabled = true
        this.setState({ image: image.src, candidate: res, editDisabled:disabled })
        console.log(this.state.editDisabled)
      })
  }
  changeExpertiseRating = (e) => {
    let temp = this.state.candidate
    temp.candidateExpertiseLevel = e.target.value
    this.setState({ candidate: temp })
  }
  changeStackName = (e, index) => {
    let temp = this.state.candidate;
    temp.expertIn[index].value = e.target.value
    this.setState({ candidate: temp })
  }
  changeStackRating = (e, index) => {
    let temp = this.state.candidate;
    temp.expertIn[index].rating = e.target.value
    this.setState({ candidate: temp })
  }
  changeEdit = () => {
    this.setState({ edit: true })
  }
  changeName = (e) => {
    let temp = this.state.candidate;
    temp.name = e.target.value;
    this.setState({ candidate: temp })
  }
  changeChallengeSolved = (e) => {
    let temp = this.state.candidate;
    temp.challengesSolved = e.target.value;
    this.setState({ candidate: temp })
  }
  save = async (candidate) => {
    console.log(candidate)
    await fetch(`http://localhost:8000/candidate/${this.state.candidate._id}`, {
      method: "PUT",
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': "*"
      },
      body: JSON.stringify({
        name: candidate.name,
        candidateExpertiseLevel: candidate.candidateExpertiseLevel,
        challengesSolved: candidate.challengesSolved,
        expertIn: candidate.expertIn,
      })
    })
    this.setState({ edit: false })

  }
  addStack = () => {
    let temp = this.state.candidate;
    temp.expertIn.push({ rating: 0, value: "" })
    this.setState({ candidate: temp })
  }
  deleteCandidateStack = (index) => {
    let temp = this.state.candidate;
    temp.expertIn.splice(index, 1)
    this.setState({ candidate: temp })
  }
  render() {
    return (
      this.state.candidate && this.state.candidate.expertIn && !this.state.edit ?
        <Container style={{ display: "flex", flexDirection: "row", justifyContent: "center", paddingTop: 50 }}>
          <Card style={{ display: "flex", flexDirection: "row", flexWrap: "wrap", justifyContent: "space-evenly" }}>
            <CardMedia
              style={{ height: 500, width: 500 }}
              image={this.state.image}
              title="Contemplative Reptile"
            />
            <div style={{ display: "flex", height: 500, width: 300, flexDirection: "column", overflow: "scroll" }}>
              <CardContent>
                <Typography component="h6" variant="h6">
                  {this.state.candidate.name}
                </Typography>
                <div style={{ display: "flex" }}>
                  <span style={{ width: "50%" }}>
                    <Typography variant="subtitle1">
                      Votes:
                </Typography>
                  </span>
                  <span style={{ width: "50%" }}>
                    <Typography style={{ paddingLeft: 10 }} variant="subtitle1">
                      {this.state.candidate.votes}
                    </Typography>
                  </span>
                </div>
                <div style={{ display: "flex" }}>
                  <span style={{ width: "50%" }}>
                    <Typography variant="subtitle1">
                      Challenge : Solved
                </Typography>
                  </span>
                  <span style={{ width: "50%" }}>
                    <Typography style={{ paddingLeft: 10 }} variant="subtitle1">
                      {this.state.candidate.challengesSolved}
                    </Typography>
                  </span>
                </div>
                <div style={{ display: "flex" }}>
                  <span style={{ width: "50%" }}>
                    <Typography variant="subtitle1">
                      Expertise:
                     </Typography>
                  </span>
                  <span style={{ width: "50%" }}>
                    <Rating style={{ padding: 4 }} disabled={!this.state.edit} value={this.state.candidate.candidateExpertiseLevel} />
                  </span>
                </div>
                <Typography variant="h6">
                  Stack:
                </Typography>
                {
                  this.state.candidate.expertIn.map((stack, index) =>
                    <div style={{ display: "flex" }}>
                      <Typography style={{ paddingLeft: 5 }} variant="subtitle1">
                        {index + 1}.
                     </Typography>
                      <span style={{ width: "50%" }}>
                        <Typography variant="subtitle1">
                          {stack.value}
                        </Typography>
                      </span>
                      <span style={{ width: "50%" }}>
                        <Rating style={{ padding: 4 }} disabled={!this.state.edit} value={stack.rating} />
                      </span>
                    </div>
                  )}

              </CardContent>
              {/* <CardActionArea style={{ display: "flex", justifyContent: "center" }}> */}
                <Button disabled={this.state.editDisabled} onClick={() => this.changeEdit()}>
                  Edit {this.state.editDisabled}
              </Button>
              {/* </CardActionArea> */}
            </div>
          </Card>
        </Container>
        : this.state.candidate && this.state.candidate.name ? <div>
          <Container style={{ display: "flex", flexDirection: "row", justifyContent: "center", paddingTop: 50 }}>
            <Card style={{ display: "flex", flexDirection: "row", flexWrap: "wrap", justifyContent: "space-evenly" }}>
              <CardMedia
                style={{ height: 500, width: 500 }}
                image={this.state.image}
                title="Contemplative Reptile"
              />
              <div style={{ display: "flex", height: 500, width: 300, flexDirection: "column", overflow: "scroll" }}>
                <CardContent>
                  <TextField
                    onChange={e => this.changeName(e)}
                    value={this.state.candidate.name}
                  />
                  <div style={{ display: "flex" }}>
                    <span style={{ width: "50%" }}>
                      <Typography variant="subtitle1">
                        Votes:
                </Typography>
                    </span>
                    <span style={{ width: "50%" }}>
                      <Typography style={{ paddingLeft: 10 }} variant="subtitle1">
                        {this.state.candidate.votes}
                      </Typography>
                    </span>
                  </div>
                  <div style={{ display: "flex" }}>
                    <span style={{ width: "50%" }}>
                      <Typography variant="subtitle1">
                        Challenge Solved:
                </Typography>
                    </span>
                    <span style={{ width: "50%" }}>
                      <TextField
                        style={{ paddingLeft: 10 }}
                        value={this.state.candidate.challengesSolved}
                        onChange={e => this.changeChallengeSolved(e)}
                        type="number"
                      />
                    </span>
                  </div>
                  <div style={{ display: "flex" }}>
                    <span style={{ width: "50%" }}>
                      <Typography variant="subtitle1">
                        Expertise:
                     </Typography>
                    </span>
                    <span style={{ width: "50%" }}>
                      <Rating style={{ padding: 4 }} onChange={e => this.changeExpertiseRating(e)} value={this.state.candidate.candidateExpertiseLevel} />
                    </span>
                  </div>
                  <Typography variant="h6">
                    Stack:
                </Typography>
                  {
                    this.state.candidate.expertIn.map((stack, index) =>
                      <div style={{ display: "flex" }}>
                        <Typography style={{ paddingLeft: 5 }} variant="subtitle1">
                          {index + 1}.
                     </Typography>
                        <span style={{ width: "50%" }}>
                          <TextField
                            value={stack.value}
                            onChange={e => this.changeStackName(e, index)}
                            required={true}
                          />
                        </span>
                        <span style={{ width: "50%" }}>
                          <Rating style={{ padding: 4 }} onChange={e => this.changeStackRating(e, index)} value={stack.rating} />
                        </span>
                        <span>
                          <IconButton onClick={e => this.deleteCandidateStack(index)} aria-label="delete">
                            <DeleteIcon />
                          </IconButton>
                        </span>
                      </div>
                    )}
                  <Button onClick={e => this.addStack()}>Add More Stack</Button>

                </CardContent>
                <CardActionArea style={{ display: "flex", justifyContent: "center" }}>
                  <CardActions disabled={this.state.editDisabled} onClick={() => this.save(this.state.candidate)}>
                    Save
                  </CardActions>

                </CardActionArea>
              </div>
            </Card>
          </Container>
        </div> : <div></div>
    )
  }
}

export default Candidate;