import React from 'react';
import './App.css';
import Paper from '@material-ui/core/Paper';
import Table from "./components/Table";
import Grid from '@material-ui/core/Grid'
import RegisterUser from "./components/Dialogbox";
import Ip from "ip";
import AuthDialog from './components/authDialog';
import { Typography } from '@material-ui/core';

class App extends React.Component {
  state = {
    candidates: [],
    owner: {},
    auth: false,
    registeredUser: false,
    disableVote: false
  }
  componentDidMount() {
    fetch('http://localhost:8000/candidate')
      .then(res => res.json())
      .then(res => {
        console.log(res)
        let owner = res.filter(res => res.ip === Ip.address())
        let registeredUser = false;
        owner = owner.length ? owner[0] : {};
        (owner.ip === Ip.address()) ? registeredUser = true : registeredUser = false
        console.log(owner.hasVoted)
        this.setState({ candidates: res, owner: owner, registeredUser: registeredUser,disableVote:owner.hasVoted })
      })
  }
  deleteCandidate = async (candidate, index) => {
    await fetch(`http://localhost:8000/candidate/${candidate._id}`, {
      method: "DELETE",
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': "*"
      },
    })
      .then(res => res.json())
      .then(res => {
        var disableVote;
        var auth = true;
        if (candidate.ip === Ip.address()) {
          disableVote = true;
          auth= false;
        }
        var temp = this.state.candidates;
        temp.splice(index, 1);
        this.setState({ candidates: temp, registeredUser: false, disableVote: disableVote, auth: auth })
      })
  }
  vote = async (candidate, index) => {
    console.log(this.props.owner)
    await fetch(`http://localhost:8000/candidate/${this.state.owner._id}`, {
      method: "PUT",
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': "*"
      },
      body: JSON.stringify({ hasVoted: true })
    })
      .then(res => res.json())
      .then(res => {
        var temp = this.state.candidates;
        temp[index].hasVoted = true;
        var tempOwner = this.state.owner;
        tempOwner.hasVoted = true;
        this.setState({ candidate: temp ,owner:tempOwner})
      }).catch(err => alert(err))
    await fetch(`http://localhost:8000/candidate/${candidate._id}`, {
      method: "PUT",
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': "*"
      },
      body: JSON.stringify({ votes: this.state.candidates[index].votes + 1 })
    })
      .then(res => res.json())
      .then(res => {
        var temp = this.state.candidates
        temp[index].votes = temp[index].votes + 1;
        this.setState({ candidates: temp })
      }).catch(err => alert(err))
  }
  addCandidate = (candidate) => {
    var temp = this.state.candidates;
    var disableVote;
    temp.push(candidate);
    this.setState({ candidates: temp, registeredUser: true, auth: false });
  }
  authrisationVerified = () => {
    this.setState({ auth: true })
  }
  render() {
    return (
      <div>
        <Paper elevation={3} style={{ width: "100%", height: 60, background: "content-box" }}>
          <Grid container
            direction="row"
            justify="space-around"
            alignItems="center"
          >
            <Grid item spacing={3}>
            </Grid>
            <Grid item style={{ paddingTop: 10 }} spacing={6}>
              {
                (this.state.auth) ?
                  <RegisterUser auth={this.state.auth} addCandidate={this.addCandidate.bind(this)} /> : <></>
              }
            </Grid>
            <Grid item style={{ paddingTop: 10 }} spacing={3}>{
              (this.state.registeredUser) ?
                (this.state.auth) ? <Typography style={{ color: "green", paddingTop: 10 }}>Admin</Typography> :
                  <AuthDialog authrisationVerified={this.authrisationVerified.bind(this)} /> :
                <RegisterUser addCandidate={this.addCandidate.bind(this)} />}
            </Grid>
          </Grid>
        </Paper>
        <Grid container
          direction="row"
          justify="center"
        >
          <Grid spacing={3}></Grid>
          <Grid spacing={6}>
            <Table
              candidates={this.state.candidates}
              vote={this.vote.bind(this)}
              deleteCandidate={this.deleteCandidate.bind(this)}
              owner={this.state.owner}
              auth={this.state.auth}
              disableVote={this.state.disableVote}
              registeredUser={this.state.registeredUser}
            />
          </Grid>
        </Grid>
        <Grid spacing={3}></Grid>
      </div>
    )
  }
}

export default App;
